<template lang="">
  <div id="calendar">
    <div class="calendar-img" :style="{'background-image': 'url('+ require('../assets/img/calendar/'+ currentDate.month + '.jpg') +')'}"></div>
      <div class="calendar-container" :style="{'background-image': 'url('+ require('../assets/img/calendar/'+ currentDate.month + '.jpg') +')'}">
      <header>
        <div class="current-date">
          <div class="current-day">
            {{ weekdayNames[currentDay] }}
          </div>
          <div class="today">
            <div class="arrow-up" @click="dateUp()"></div>
            <div class="arrow-up" @click="monthUp()"></div>
            <div class="arrow-up" @click="currentDate.year += 1"></div>
            <div>{{ currentDate.date }}</div>
            <div>{{ month[currentDate.month]  }}</div>
            <div>{{ currentDate.year  }}</div>          
            <div class="arrow-down" @click="dateDown()"></div>
            <div class="arrow-down" @click="monthDown()"></div>
            <div class="arrow-down" @click="currentDate.year -= 1"></div>
  
          </div>
        </div>
      </header>
      <section>
        <div class="weekdays">
          <div class="weekday" v-for="(weekday, index) in weekdays" :key="index">
            {{ weekday }}
          </div>
        </div>
        <div class="date">
          <div class="day-hidden" v-for="(n, index) in (firstMonthDay -1)" :key="'prev'+index">
          {{ (prevMonthDays +1) - firstMonthDay + n }}
          </div>
          <div class="day" @click="currentDate.date = n" :class="{active: n === currentDate.date}" v-for="(n, index) in currentMonthDays" :key="'day'+index">
            {{ n }}
          </div>
          <div class="day-hidden" v-for="(n, index) in (43 - (currentMonthDays + firstMonthDay))" :key="'next'+index">
            {{ n }}
          </div>
        </div>
      </section>
    </div>
  </div>
</template>
<script>

export default {
  name: 'CalendarView',
  data() {
    return {
      weekdays: ['Mo', 'Th', 'We', 'Th', 'Fr', 'Sa', 'Su'],
      weekdayNames: ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'],
      month: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
      currentDate: {
        date: 0,
        month: 0,
        year: 0
      }
    }
  },
  computed: {
    prevMonthDays() {
      let year = this.currentDate.month === 0 ? this.currentDate.year - 1 : this.currentDate.year;
      let month = this.currentDate.month === 0 ? 11 : this.currentDate.month;
      return new Date(year, month, 0).getDate();
    },
    firstMonthDay() {
      let firstDay = new Date(this.currentDate.year, this.currentDate.month, 1).getDay();
      if (firstDay === 0) firstDay = 7;
      return firstDay;
    },
    currentDay() {
      return new Date(this.currentDate.year, this.currentDate.month, this.currentDate.date).getDay();
    },
    currentMonthDays() {
      return new Date(this.currentDate.year, this.currentDate.month + 1, 0).getDate();
    }
  },
  methods: {
    getCurrentDate() {
      let today = new Date();
      this.currentDate.date = today.getDate();
      this.currentDate.month = today.getMonth();
      this.currentDate.year = today.getFullYear();
    },
    dateUp() {
      if (this.currentDate.date === this.currentMonthDays) {
        this.currentDate.date = 1;
        this.monthUp();
      }
      else {
        this.currentDate.date += 1;
      }
    },
    dateDown() {
      if (this.currentDate.date === 1) {
        this.currentDate.date = this.prevMonthDays;
        this.monthDown();
      }
      else {
        this.currentDate.date -= 1;
      }
    },
    monthUp() {
      if (this.currentDate.month === 11) {
        this.currentDate.month = 0;
        this.currentDate.year += 1;
      }
      else {
        this.currentDate.month += 1;
      }
    },
    monthDown() {
      if (this.currentDate.month === 0) {
        this.currentDate.month = 11;
        this.currentDate.year -= 1;
      }
      else {
        this.currentDate.month -= 1;
      }
    }
  },
  created() {
    this.getCurrentDate();
  }
}
</script>
<style lang="scss" scoped>
@import url('https://fonts.googleapis.coM/css?family=Anton');

@mixin calendar-layout($padding) {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(50px, 1fr));
  padding: $padding;

  div {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 30px;
    color: #fff;
    border-radius: 5px;
  }
}

@mixin arrow-style() {
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  cursor: pointer;
}

#calendar {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0 auto;

  .calendar-img {
    width: 100%;
    height: 100vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    filter: blur(20px);
    -webkit-filter: blur(20px);
  }

  .calendar-container {
    position: absolute;
    //width: 400px;
    height: 100vh;
    padding: 50px 0px;
    background-color: #efefef;
    font-family: 'Anton';
    background-size: cover;
    user-select: none;
    
    @media all and (min-width: 400px) {
      width: 400px;
      overflow: hidden;
    }

    header {
      display: flex;
      justify-content: center;
      align-items: top;
      height: 300px;
      padding: 20px 0 0;
      text-align: center;
      overflow: hidden;
      color: #efefef;
      text-shadow: 1px 1px 1px #222, 1px 1px 1px #222, -1px 1px 1px #222, -1px -1px 1px #222;

      //background: rgba(150, 18, 18, 0.37);
      .current-date {
        width: 300px;
      }

      .arrow-up {
        @include arrow-style();
        border-bottom: 10px solid #fff;

        &:active {
          border-bottom: 10px solid rgba(0, 0, 0, .4);
        }
      }

      .arrow-down {
        @include arrow-style();
        border-top: 10px solid #fff;

        &:active {
          border-top: 10px solid rgba(0, 0, 0, .4);
        }
      }

      .today {
        display: grid;
        //grid-template-columns: 40px auto 70px;
        grid-template-columns: 1fr 2fr 1fr;

        div {
          display: flex;
          justify-content: center;
          margin: 0 auto;

          @media all and (max-width: 400px) {
            font-size: 1.6rem;
          }
          @media all and (max-width: 300px) {
            font-size: .8rem;
          }
        }
      }

      .current-day {
        font-size: 4rem;

        @media all and (max-width: 400px) {
          font-size: 3rem;
        }
      }

      .today {
        font-size: 2rem;
      }
    }

    .weekdays {
      @include calendar-layout(10px 20px 10px);
      background-color: rgba(0, 0, 0, .5);
      border-bottom: 1px solid #fff;

      @media all and (max-width: 400px) {
        display: none;
      }

      div:nth-child(7n) {
        color: #D43541;
      }
    }

    .date {
      @include calendar-layout(10px 20px 10px);
      background-color: rgba(0, 0, 0, .7);

      .active {
        background-color: #fff;
        color: #2A4C50;
      }

      .day {
        cursor: pointer;

        &:hover {
          background-color: #fff;
          color: #2A4C50;
        }

        &:nth-child(7n) {
          color: #D43541;

          &:hover {
            background-color: #fff;
            color: #D43541;
          }
        }
      }

      .day-hidden {
        opacity: .4;

        &:nth-child(7n) {
          color: #D43541;
        }
      }
    }
  }
}
</style>